import { useTheme } from '@zigurous/react-components';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import Dock from './Dock';
import Header from './Header';
import Metadata from './Metadata';

const Page = ({
  children,
  className,
  dockLinks,
  hideDock = false,
  hideHeader = false,
  id,
  location,
  metadata,
}) => {
  const [theme, _, toggleTheme] = useTheme('light');
  return (
    <div className="app" data-theme={theme}>
      <Metadata {...metadata} />
      {!hideHeader && <Header location={location} />}
      <main className={classNames('page', className)} id={id}>
        {children}
      </main>
      {!hideDock && (
        <Dock
          secondaryLinks={dockLinks}
          theme={theme}
          toggleTheme={toggleTheme}
        />
      )}
    </div>
  );
};

Page.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  hideDock: PropTypes.bool,
  hideHeader: PropTypes.bool,
  id: PropTypes.string,
  location: PropTypes.object,
  metadata: PropTypes.object,
};

export default Page;
